@font-face {
  font-family: 'iconfont';
  src: url('//at.alicdn.com/t/font_1473821492_7291307.eot'); /* IE9*/
  src: url('//at.alicdn.com/t/font_1473821492_7291307.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('//at.alicdn.com/t/font_1473821492_7291307.woff') format('woff'), /* chrome、firefox */
  url('//at.alicdn.com/t/font_1473821492_7291307.ttf') format('truetype'), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
  url('//at.alicdn.com/t/font_1473821492_7291307.svg#iconfont') format('svg'); /* iOS 4.1- */
}

.iconfont{font-family:"iconfont" !important;font-style:normal;display:inline-block;-webkit-font-smoothing:antialiased;-webkit-text-stroke-width:.2px;-moz-osx-font-smoothing:grayscale; line-height: normal;}

html,body {height: 100%; position: relative; }
* {box-sizing: border-box;  -webkit-font-smoothing: antialiased;}
#app {
  min-width: 1080px;
  position: relative;
  min-height: 100%;
}

/*
* github: https://github.com/git-lt/Tool.css/blob/master/Tool.css
*/
/* percent width value */
	.pct10{width:10%;} .pct20{width:20%;} .pct25{width:25%;} .pct30{width:30%;} .pct33{width:33.3%;} .pct40{width:40%;} .pct50{width:50%;} .pct60{width:60%;} .pct70{width:70%;} .pct80{width:80%;} .pct90{width:90%;} .pct100{width:100%;}

/* font-size */
	.f0{font-size:0;} .f10{font-size:10px;} .f12{font-size:12px;} .f13{font-size:13px;} .f14{font-size:14px;} .f16{font-size:16px;} .f18{font-size:18px;}
  .f24{font-size: 24px;}

/*line-height*/
	.lh24 { line-height: 24px;} .lh28 { line-height: 28px;} .lh30 { line-height: 30px;}

/* font-style */
	.n{font-weight:400; font-style:normal; white-space: normal;} .b{font-weight:bold;}.i{font-style:italic;}.del {text-decoration: line-through;}

/* font-family */
	.fa{font-family:Arial;}

/* margin */
	.m0  {margin:0px;} .m5 {margin:5px;} .m10 {margin:10px;} .m15 {margin:15px;} .m20 {margin:20px;} .m25 {margin:25px;} .m30 {margin:30px;}
	.mt5 {margin-top:5px;} .mt10 {margin-top: 10px;} .mt15 {margin-top: 10px;} .mt20 {margin-top: 20px;} .mt25 {margin-top: 20px;} .mt30 {margin-top: 30px;}
	.ml5 {margin-left:5px;} .ml10 {margin-left:10px;} .ml15 {margin-left:15px;} .ml20 {margin-left:20px;} .ml25 {margin-left:20px;} .ml30 {margin-left:30px;}
	.mb5 {margin-bottom:5px;} .mb10 {margin-bottom:10px;} .mb15 {margin-bottom:10px;} .mb20 {margin-bottom:20px;} .mb25 {margin-bottom:20px;} .mb30 {margin-bottom:30px;}
	.mr5 {margin-right:5px;} .mr10 {margin-right:10px;} .mr15 {margin-right:15px;} .mr20 {margin-right:20px;} .mr25 {margin-right:25px;} .mr30 {margin-right:30px;}

/* padding */
	.p0  {padding:0px;} .p5 {padding:5px;} .p10 {padding:10px;} .p15 {padding:15px;} .p20 {padding:20px;} .p25 {padding:25px;} .p30 {padding:30px;}
	.pt5 {padding-top: 5px;} .pt10 {padding-top: 10px;} .pt15 {padding-top: 15px;} .pt20 {padding-top: 20px;} .pt25 {padding-top: 25px;} .pt30 {padding-top: 30px;}
	.pl5 {padding-left: 5px;} .pl10 {padding-left: 10px;} .pl15 {padding-left: 15px;} .pl20 {padding-left: 20px;}.pl25 {padding-left: 25px;} .pl30 {padding-left: 30px;}
	.pb5 {padding-bottom: 5px;} .pb10 {padding-bottom: 10px;} .pb15 {padding-bottom: 15px;} .pb20 {padding-bottom: 20px;} .pb25 {padding-bottom: 25px;} .pb30 {padding-bottom: 30px;}
	.pr5 {padding-right: 5px;} .pr10 {padding-right: 10px;} .pr15 {padding-right: 15px;} .pr20 {padding-right: 20px;} .pr25 {padding-right: 25px;} .pr30 {padding-right: 30px;}

/*display*/
	.dn{display:none;} .di{display:inline;} .db{display:block;} .dib{display:inline-block;}

/*border*/
	.bdr {border:1px solid #f2f2f2;} .bdr-l {border-left:1px solid #f2f2f2;} .bdr-r {border-right:1px solid #f2f2f2;} .bdr-t {border-top:1px solid #f2f2f2;} .bdr-b {border-bottom:1px solid #f2f2f2;}
  .bdr0 {border:0 !important;}

/*color*/
	.c-3 {color:#333;} .c-6 {color:#666;} .c-8 {color:#888;} .c-9 {color:#999;}
	.c-rd {color: #F44336;} .c-bl {color: #0894EC;} .c-wt {color: #FFFFFF;} .c-gr {color: #4CD964;} .c-or {color: #FF6600;}

/*backgroud-color*/
	.bg-wt { background-color: #fff; }.bg-f5 { background-color: #f5f5f5; }

/*float & position*/
	.l {float: left;}.r {float: right;}
	.clearfix:before,.clearfix:after {display: table;content: " ";}.clearfix:after {clear: both;}
	.rel {position: relative;}.abs {position: absolute;}

/* align */
	.vt {vertical-align: top;}.vb {vertical-align: bottom;}.vm {vertical-align: middle;}
	.tc {text-align: center;} .tl {text-align: left;} .tr {text-align: right;}

/* overflow */
	.ovh{overflow:hidden;} .ova{overflow:auto;}

/* visibility */
	.vh{visibility:hidden;}.vv{visibility:visible;}

/*hide scrollbar*/
	.no-scroll {overflow: hidden !important; height: 100% !important;}.no-scroll body {overflow: hidden; height: 100%;}

/*divider line*/
	.dvd-x {display: block;font-size: 0; height: 1px; clear: both;}
	.dvd-y {display:inline-block; font-size: 0; width: 1px;}

/* ellipsis */
	.ell{text-overflow:ellipsis; white-space:nowrap; overflow:hidden;}

/*transition*/
	.trans{-ms-transition:all 0.2s; -webkit-transition:all 0.2s; transition:all 0.2s;}






// 公共样式
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
[v-cloak] {
  display: none;
}
a {
  text-decoration: none;
  color: #C0CCDA;
}
.router-link-active {
  color: #20a0ff;
}
.fix{*zoom:1;}
.fix:after{display:table; content:''; clear:both;}
.card-box {
  border: 1px solid #eaeefb;
  border-radius: 4px;
  -webkit-transition: .2s;
  transition: .2s;
  background-color: #fff;
  padding: 20px 15px;
}
.card-box:hover{
  box-shadow: 0 0 8px 0 rgba(232,237,250,.6),0 2px 4px 0 rgba(232,237,250,.5);
}
// 重写样式
.el-menu--horizontal>.el-menu-item:hover, .el-menu--horizontal>.el-submenu.is-active .el-submenu__title, .el-menu--horizontal>.el-submenu:hover .el-submenu__title{
    border-bottom: 2px solid #20a0ff;
}
.el-menu > .el-menu-item,.el-submenu__title {
  font-size: 13px;
}
.el-menu .iconfont {
  margin-right:6px;
}

.el-menu-item, .el-submenu__title {
  height: 42px;
  line-height: 42px;
}
.el-submenu .el-menu-item {
  height: 42px;
  line-height: 42px;
  font-size: 12px;
}
.el-menu--horizontal.el-menu--dark .el-submenu .el-menu-item.is-active, .el-menu-item.is-active{
  border-left: 2px solid;
  background: #1F2F3D;
}

.el-menu--horizontal .el-menu-item{
  height: 46px;
  line-height: 46px;
  border-bottom: 2px solid transparent;
}
.el-menu--horizontal .el-submenu .el-submenu__title{
  height: 46px;
  line-height: 46px;
  border-bottom: 2px solid transparent;
}
.el-menu--horizontal .el-submenu>.el-menu{
  top:46px;
}

.el-tabs {
  display: block;
}

/*ui-loading*/
.ui-loading-sm {cursor: default; opacity:.6 !important; color: transparent !important; pointer-events:none; position:relative; }
.ui-loading-sm:after{content:''; display: block; width: 1.2em; height: 1.2em; position:absolute; left: 50%; top:50%; margin-left: -.6em; margin-top: -.6em; border-width:.2em; border-style:solid; border-color:#fff rgba(0,0,0,.1) rgba(0,0,0,.2) rgba(0,0,0,.2); border-radius:100rem; -webkit-animation: loading-spin .6s infinite linear;-o-animation: loading-spin .6s infinite linear;-ms-animation: loading-spin .6s infinite linear; animation: loading-spin .6s infinite linear; box-shadow: 0 0 0 1px transparent; }

.ui-loading-lg {cursor: default; pointer-events:none; position:relative; }
.ui-loading-lg:before{content:''; display: block; position:absolute; left: 0; top: 0; right: 0; bottom: 0; background-color:#fff; opacity:.8; z-index:100; }
.ui-loading-lg:after {content:''; display: block; width: 3em; height: 3em; position:absolute; left: 50%; top:50%; margin-left: -1.5em; margin-top: -1.5em; border-width:.25em; border-style:solid; border-color:#fff rgba(0,0,0,.1) rgba(0,0,0,.2) rgba(0,0,0,.2); border-radius:100rem; -webkit-animation: loading-spin .6s infinite linear;-ms-animation: loading-spin .6s infinite linear;-o-animation: loading-spin .6s infinite linear; animation: loading-spin .6s infinite linear; z-index:101; box-shadow: 0 0 0 1px transparent; }
@-webkit-keyframes loading-spin{0% {transform:rotate(0degb)} 100% {transform:rotate(360deg)} }
@-ms-keyframes loading-spin{0% {transform:rotate(0degb)} 100% {transform:rotate(360deg)} }
@-o-keyframes loading-spin{0% {transform:rotate(0degb)} 100% {transform:rotate(360deg)} }
@keyframes loading-spin{0% {transform:rotate(0degb)} 100% {transform:rotate(360deg)} }



